<template>
  <div class="col-sm-9 col-md-10 col-xs-10 col-xs-offset-2 col-md-offset-2 col-sm-offset-3 row SingleBlog">
    <div class="box">
    <h1 class="text-center text-capitalize">{{blog.title}}</h1>
    <p>{{blog.body}}</p>
    <div class="text-right operation">
      <router-link v-bind:to="{name:'edit',params:{id:id}}" tag="a" href="javascript:;">
        <span class="glyphicon glyphicon-edit" :blogs='blogs'>编辑</span>
      </router-link>
      <a href="javascript:;" v-on:click="deletebolg"><span class="
glyphicon glyphicon-trash" >删除</span></a>
    </div></div>
  </div>
</template>

<script>
export default {
  name: 'SingleBlog',
  props:['blogs'],
  data:function(){
    return{
      id:this.$route.params.id,
      blog:{}
    }
  },
  created:function(){
    /*this.$http.get("https://jsonplaceholder.typicode.com/posts/"+this.id)
    .then(res=>{  
        this.blog =  res.body;  
        console.log(this.blog);
    })*/
    console.log(this.blogs);
    for(let i = 0;i < this.blogs.length;i++){
      if(this.blogs[i].id == this.id){
        this.blog = this.blogs[i];
      }     
    }
  },
  methods:{
    deletebolg:function(){
      this.$http.delete("https://jsonplaceholder.typicode.com/posts/"+this.id).then(res=>{
          this.$router.push({name:'index'});
      })
    }
  }
}
</script>


<style scoped>
p{
  /*padding: 20px;*/
}
.box{
  margin: 10px;
  padding: 10px;
  background: #e7e7e7;
}
.operation a{
  margin: 0 10px;
  text-decoration: none;
  color: #000;
}
</style>
