<template>
  <div id="app" class="container-fluid row">
    <blogHeader></blogHeader>
      <router-view @pushBlog='pushBlog' :blogs='blogs'></router-view>
  </div>
</template>

<script>
import blogHeader from './components/hearder.vue'

export default {
  name: 'app',
  data:function(){
    return{
      blogs:[]
    }
  },
  components:{
    blogHeader
  },
  created:function () {
    this.$http.get("https://jsonplaceholder.typicode.com/posts")
    .then(res=>{  
        this.blogs =  res.body.slice(0,10);  
        console.log(this.blogs);
    })
  },
  methods:{
    pushBlog:function(data){
      data.id = Date.now();
    this.blogs = [data,...this.blogs];
      console.log(this.blogs);
    }
  }
}
</script>

