import ShowBlogs from './components/blogBrowse.vue'
import Blogging from './components/Blogging.vue'
import SingleBlog from './components/SingleBlog.vue'
import CompileBlog from './components/CompileBlog.vue'
export default[
	{path:'/',name:'home',component:ShowBlogs,redirect: '/blogs'},
	{path:'/blogs/',name:'index',component:ShowBlogs},
	{path:'/blogs/add',name:'add',component:Blogging},
	{path:'/blogs/blog/:id',name:'blog',component:SingleBlog},
	{path:'/blogs/edit/:id',name:'edit',component:CompileBlog}
]