<template>
  <div class="CompileBlog">
    <div class="col-sm-9 col-md-10 col-xs-offset-2 col-md-offset-2 col-sm-offset-3 Blogging">
    <h1 class="text-center">编辑博客</h1>
    <form>
      <div class="form-group">
        <label for="title">博客标题</label>
        <input type="text" class="form-control" id="title" placeholder="请输入标题" v-model="blog.title">
      </div>
      <div class="form-group">
        <label for="author">博客作者</label>
        <input type="text" class="form-control" id="author" placeholder="请输入作者" v-model="blog.author">
      </div>
      <div class="form-group">
        <label for="context">博客内容</label>
        <textarea class="form-control" rows="5" id="context" placeholder="请输入内容"  v-model="blog.body"></textarea>
      </div>
      <div class="form-group">
        <label for="cation">博客分类</label>
          <select class="form-control" id="cation"  v-model="blog.cation">
            <option value="生活类">生活类</option>
            <option value="技术类">技术类</option>
            <option value="娱乐类">娱乐类</option>
            <option value="音乐类">音乐类</option>
            <option value="影视类">影视类</option>
          </select>
      </div>
      <button type="submit" class="btn btn-success btn-default btn-sm btn-lg btn-block" @click.prevent="put()">编辑</button>
    </form>
  </div>
  </div>
</template>

<script>
export default {
  name: 'CompileBlog',
  props:['blogs'],
  data:function(){
    return{
      id:this.$route.params.id,
      blog:{}
    }
  },
  created:function(){
    // this.$http.get("https://jsonplaceholder.typicode.com/posts/"+this.id)
    // .then(res=>{  
    //     this.blog =  res.body;  
    //     console.log(this.blog);
    // })
    for(let i = 0;i < this.blogs.length;i++){
      if(this.blogs[i].id == this.id){
        this.blog = this.blogs[i];
      }     
    }
  },
  methods:{
    put:function(){
      this.$http.put("https://jsonplaceholder.typicode.com/posts/1",{
        title:this.blog.title,
        body:this.blog.body,
        userId:1,
      })
      .then(res=>{
        res.body.id = this.id;
        for(let i = 0;i < this.blogs.length;i++){

        console.log(this.blogs[i]);
          if(this.blogs[i].id == this.id){
            // console.log(res);
            this.blogs[i] = res.body;
          }     
        }
        this.$router.push({name:'index'});
      })
    }
  }
}
</script>


