<template>
  <div class="col-sm-9 col-md-10 col-xs-10 col-xs-offset-2 col-md-offset-2 col-sm-offset-3 text-center blogBrowse">
      <h2 class="text-certer" >博客总览</h2>
      <form action="">
        <div class="form-group">        
          <div class="col-xs-12">
            <input type="text" class="form-control" placeholder="请输入搜索内容" v-model="inquire">
          </div> 
        </div>
      </form>
      <section class="row">
          <div class="col-md-4 col-sm-6 col-xs-12 blog" v-for="(blog,item) in filteredBlogs" :key="item">
              <router-link v-bind:to="{name:'blog',params:{id:blog.id}}" tag="article" :blogs='blogs'>
                <h3>{{blog.title}}</h3>
                <p class="text-left blogContext">{{blog.body}}</p>
                <span class="author">作者</span>
                <span class="category">分类</span>
              </router-link>
          </div>
      </section>
  </div>
</template>

<script>
export default {
  name: 'blogBrowse',
  props:{
    blogs:{
      type:Array,
      required:true
    }
  },
  data:function(){
    return{
      blog:[],
      inquire:''
    }
  }, 
  computed:{
    filteredBlogs:function(){
      return this.blogs.filter((blogs)=> {
          return blogs.title.match(this.inquire);
      })
    }
  }
}
</script>

<style scoped>
.blog{
  /*background-color: red;*/
  height: 180px;
  margin-top: 25px;
  cursor: pointer;
  /*margin: 30px;*/
}
.blog>article{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  /*background: #acacac;*/
  padding: 8px;
  box-shadow: 0px 0px 5px #808080;
  position: relative;

}
h3{
  margin: 0;
  /*width: 20%;*/
  margin-bottom: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.blogContext{
  height: 50%;
  margin: 0;
  /*background: #abcdef;*/
  text-indent: 2em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
.author{
  position: absolute;
  bottom: 5px;
  left: 10px;
}
.category{
  position: absolute;
  bottom: 5px;
  right: 10px;
}
</style>
